@font-face {
  src: url("font/GalanoGrotesqueAltRegular.0e190648.woff2") format("woff2");
  font-family: GalanoGrotesqueAlt;
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesqueAltSemiBold.710dbe2b.woff2") format("woff2");
  font-family: GalanoGrotesqueAlt;
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesque-Bold.2be9a942.woff2") format("woff2");
  font-family: GalanoGrotesque;
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("font/GalanoGrotesque-Black.d7ff6920.woff2") format("woff2");
  font-family: GalanoGrotesque;
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

:root {
  --font-family-primary: "GalanoGrotesqueAlt", sans-serif;
  --font-family-secondary: "GalanoGrotesque", sans-serif;
  --content-width: 1110px;
  --content-wide: 1742px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --container-wide: calc(var(--content-wide)  + (var(--container-offset) * 2));
  --color-white: #fff;
  --color-dark-400: #1a1a1a;
  --color-gray-50: #f1f2f6;
  --color-gray-100: #b5b5b5;
  --color-gray-150: #dadada;
  --color-blue-400: #0596e8;
  --color-red-100: #fee;
  --color-gradient-main: linear-gradient(180deg, #80d0c8 0%, #0596e8 100%);
  --color-gradient-gray: linear-gradient(0deg, #ffffff4d 0%, #c3cfe24d 100%);
  --color-gradient-border: linear-gradient(var(--rotate), #dadada00 0%, #dadada 47.4%, #dadada00 100%);
  --color-aside: linear-gradient(0deg, #f5f7fa4d -49.83%, #c3cfe24d 100%);
  --shadow-ligth: 0 4px 14px #0000001a;
  --radius-main: 10px;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

.page {
  height: 100%;
  font-family: var(--font-family-primary);
  letter-spacing: -.03em;
  color: var(--color-dark-400);
  background-color: var(--color-white);
  scroll-padding-top: var(--header-height, 80px);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.6;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
}

.page--menu {
  overflow: hidden;
}

.page--menu .burger {
  transform: rotateY(180deg)translateY(-50%);
}

.page--menu .burger:before {
  background-image: url("icons/burger-close.d7b13c2d.svg");
}

.page--menu .burger__close {
  opacity: 1;
}

.page--menu .navigation__inner {
  left: 0;
}

@media only screen and (max-width: 576px) {
  .page {
    letter-spacing: normal;
    font-size: 14px;
    line-height: 1.14286;
  }
}

.container {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.container--wide {
  max-width: var(--container-wide);
}

.main {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  display: flex;
}

.main .btn {
  letter-spacing: normal;
  padding: 14px 10px;
  font-size: 18px;
  line-height: 1.22222;
}

@media only screen and (max-width: 768px) {
  .main {
    height: auto;
    flex-direction: column;
  }
}

.site-container {
  overflow-x: clip;
}

.hidden {
  display: none !important;
}

.sr-only {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.dis-scroll {
  width: 100%;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.text-gradient {
  font-family: var(--font-family-secondary);
  color: #0000;
  background: var(--color-gradient-main);
  -webkit-background-clip: text;
  background-clip: text;
}

.tooltip {
  letter-spacing: normal;
  box-shadow: var(--shadow-ligth);
  background-color: var(--color-white);
  opacity: 0;
  visibility: hidden;
  border-radius: 5px;
  padding: 5px 8px 5px 20px;
  font-size: 10px;
  line-height: 1.2;
  transition-property: opacity, visibility;
  transition-duration: .1s;
  position: absolute;
}

.no-result {
  letter-spacing: normal;
  text-align: center;
  font-size: 14px;
  line-height: 1.35714;
}

.nickname {
  font-family: var(--font-family-secondary);
  font-weight: 700;
}

.copy.copied + .tooltip {
  opacity: 1;
  visibility: visible;
}

h1 {
