}

@media only screen and (max-width: 475px) {
  .deposit__item:nth-child(3):after {
    --rotate: 270deg;
    width: 100%;
    height: 1px;
    display: block;
  }

  .deposit__item:nth-child(3) {
    grid-column: 1 / span 2;
  }
}

.deposit__number {
